h1 {
    color: lime;
}

.note {
    color: grey;
    margin-bottom: 12px;
    list-style: none;
}

.note span {
    display: inline-block;
    width: 500px;
}

.note button {
    width: 140px;
    text-align: left;
}

.error {
    color: red;
    background: lightgrey;
    font-size: 20px;
    border-style: solid;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
}